'use client';
import { motion } from 'framer-motion';
import { SparklesCore } from '@/components/ui/SparklesCore'; // adjust path if needed

export default function HeroSection() {
  const brands = ['ramp', 'hex', 'vercel', 'descript', 'cashapp', 'notion',
    'mercury', 'retool', 'remote', 'arc', 'raycast', 'runway'];

  return (
    <div className="min-h-screen w-full bg-[#0e0e0e] text-white font-sans px-6 md:px-12 overflow-hidden relative">
      
      {/* Content Layer */}
      <div className="relative z-10">
        {/* Navbar */}
        <div className="flex justify-between items-center py-6">
          <img src="/logo.svg" alt="Majarah Logo" className="w-28" />
          <div className="hidden md:flex space-x-8 text-sm text-white/80">
            <a href="#">About Us</a>
            <a href="#">Services</a>
            <a href="#">Work</a>
            <a href="#">Project</a>
          </div>
          <a
            href="#"
            className="bg-white/10 border border-white/20 px-4 py-2 text-sm rounded-full hover:bg-white/20 transition"
          >
            Contact Us
          </a>
        </div>

        {/* ⭐ Hero Text Section - limited sparkle background */}
        <div className="relative mt-16 md:mt-24 max-w-3xl mx-auto text-center z-10">
          
          {/* ✨ Sparkles just behind the hero text */}
          <SparklesCore
            className="absolute inset-0 h-full w-full z-0"
            particleColor="#3ABEFF"
            particleDensity={20}   // reduced
            minSize={0.8}
            maxSize={1.5}
            speed={1.5}
            background="transparent"
          />

          {/* Hero Text Content */}
          <div className="relative z-10">
            <p className="text-xs px-4 py-1 rounded-full border border-white/20 inline-block text-white/60 mb-6">
              Creating Digital That Matters
            </p>

            <motion.h1
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ duration: 1 }}
              className="text-4xl sm:text-5xl md:text-6xl font-semibold leading-tight"
            >
              We Don’t <span className="text-[#3ABEFF]">Just Create Content</span>
              <br />
              We Build <span className="text-[#3ABEFF]">Impact</span>
            </motion.h1>

            <p className="text-sm text-gray-400 mt-6 leading-relaxed">
              Founded by Eisa Al Habib, Emmy® award-winning filmmaker and Forbes 30 Under 30 honoree,<br />
              Majarah is a full-scale media agency delivering next-level storytelling, influencer-driven campaigns,<br />
              and cross-platform digital marketing that moves people—and brands.
            </p>

            <motion.a
              href="#"
              whileHover={{ scale: 1.05 }}
              className="inline-block mt-8 px-6 py-3 bg-gradient-to-r from-[#2A2B5F] to-[#3ABEFF] text-white rounded-full shadow-md hover:shadow-lg transition-all duration-300"
            >
              Let’s Work Together →
            </motion.a>
          </div>
        </div>

        {/* Logos */}
        <div className="mt-20 text-center">
          <p className="text-sm text-gray-400 mb-6">
            Top-tier brands and governments redefining how stories are told online.
          </p>
          <div className="grid grid-cols-3 sm:grid-cols-4 md:grid-cols-6 gap-6 place-items-center max-w-5xl mx-auto">
            {brands.map((brand, i) => (
              <img
                key={i}
                src={`/brands/${brand}.svg`}
                alt={brand}
                className="h-5 grayscale opacity-70 hover:opacity-100 transition"
              />
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}
